}

.sticky-navbar {
  position: fixed;
  top: 0;
  width: 100%;
}

#nft-body {
  height: 100vh;
  margin-top: 30px;
}

.coming-soon-page-message {
  position: absolute;
  top: 90px;
  padding-top: 90px;
  width: 100%;
}

#ecom-body {
  background-image: url("../assets/images/squabs-nft-merch.png");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

hero#home {
  display: block;
  position: relative;
  background-image: url("../assets/images/hero-buildings.png"), linear-gradient(#2b6869, #d2cfce);
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero-logo {
  z-index: 0;
  position: absolute;
  top: 0;
}

.hero-foreground {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 4;
}

.hero-squabs {
  z-index: 5;
  position: absolute;
  top: 0;
}

.canvas {
  left: 611px;
}

#park {
  z-index: 1;
}

.problems-about-connector {
  height: 70%;
  top: 74px;
  position: absolute;
  left: 18px;
  z-index: -1;
}

.solutions-about-connector {
  height: 70%;
  top: 88px;
  position: absolute;
  left: 18px;
  z-index: -1;
}

.squabs-background {
  transform: rotate(4.36deg);
  border-radius: 60px;
  border: 6px solid white;
}

.squabs-background:hover {
