  border: 0;
}

.squab-detail {
  padding-top: 180px;
}

.squab-detail:hover .squabs-background {
  @apply bg-[#CCF5E1];
  border: 0;
}

.our-audience {
  background-image: url("../assets/images/slanted-bg-white.png");
  background-size: cover;
  padding-top: 393px;
  padding-bottom: 393px;
}

.the-team {
  background-image: url("../assets/images/slanted-bg-green.png");
  background-size: cover;
  padding-top: 393px;
  padding-bottom: 393px;
}

.problems {
  background-image: url("../assets/images/about-problems-bg.png");
}

.solutions {
